/* eslint linebreak-style: ["error", "windows"] */

import React, {Component, PropTypes} from 'react';
import {connect} from 'react-redux';

class MainFocus extends Component {
  constructor(props) {
    super(props);

    this.state = ({
      inputText: this.props.mainFocus.text || ''
    });

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(e) {
    this.setState({
      inputText: e.target.value
    });
  }

  handleSubmit(e) {
    const text = e.target.value.trim();
    if (e.which === 13) {
      this.props.onSaveFocus(text);
    }
    this.setState({
      inputText: ''
    });
  }

  render() {
    console.log('this.state.inputText', this.state.inputText);
    return (
      <div>
        <div>What is your main focus of today?</div>
        <input
          type="text"
          autoFocus="true"
          value={this.state.text}
          onChange={this.handleChange}
          onKeyDown={this.handleSubmit}
          />
      </div>
    );
  }
}

MainFocus.propTypes = {
  mainFocus: PropTypes.object.isRequired,
  onSaveFocus: PropTypes.func.isRequired
};

const mapStateToProps = state => {
  return {
    mainFocus: state.mainFocus
  };
};

export default connect(mapStateToProps)(MainFocus);



